    components: {
      StyleEditor,
      ResumeEditor
    },
    data() {
      return {
        interval       : 40,
        currentStyle   : '',
        enableHtml     : false,
        fullStyle      : [
          `/*
* 大家好，我是Jack•s 代号JX
