  position: fixed; left: 0; top: 0;
  transform: rotateX(-10deg) translateZ(0px) ;
}

/* 接下来我给自己准备一个编辑器 */
.resumeEditor{
  position: fixed;
  top: 50%; left: 0;
  padding: .5em;
  margin: 2.5vw;
  width: 95vw;
  height: 48vh;
  border: 1px solid;
  background: white;
  color: #222;
  overflow: auto;
}
/* 是不是很辣(酷)眼(逼)睛(了)
* 这还完全没有辣逼(酷炫)完
* 我把下面的简历修改成了我的辣眼(苦逼)故事
*/

`,
          `
/* 这个好像差点什么
 * 是的，这是 Markdown 格式的
 * 我需要变成对 投资人 更友好的格式
 * 简单，用开源工具翻译成 HTML 就行了
 */
`
          ,
          `
/* 再对 HTML 加点样式 */
.resumeEditor{
  padding: 2em;
  transform: rotateX(20deg) translateZ(-50px) ;
}
.resumeEditor img{
  width:30vw;
}
.resumeEditor h2{
  display: inline-block;
  border-bottom: 1px solid;
  margin: 1em 0 .5em;
}
.resumeEditor ul,.resumeEditor ol{
  list-style: none;
}
.resumeEditor ul> li::before{
  content: '•';
  margin-right: .5em;
}
.resumeEditor ol {
  counter-reset: section;
}
.resumeEditor ol li::before {
  counter-increment: section;
  content: counters(section, ".") " ";
  margin-right: .5em;
}
.resumeEditor blockquote {
  margin: 1em;
  padding: .5em;
  background: #ddd;
}

/* 您可以慢慢欣赏或者转发给大佬们了 么么哒 */
` ],
        currentMarkdown: '',
        fullMarkdown   : `## 一只还没有铜臭味的创业Dog的情怀创业故事

> 前前前职-上海某某某等公司前端开(代)发(码)攻(搬)城(运)狮(工)

> 现职-四川生梨奇迹信息科技有限责任公司C(打)E(杂)O(工)

