> 怎么办，还是得屁颠地去找工作，都说人得往高处走，咱就往高端大气上档次的公司去，可能自命不凡，又可能颜值跪地，高不成低不就自己也嫌弃

> 咋办？人终得有个归属，那就创业建设家乡去吧。

> 虽然躺过金融上市公司，爬过互联网创业公司，怀着一腔热血空理论，也掩盖不了我是只屁颠创业小白。

> 摸着石头过河，苦学公司法，生吞毒鸡汤，就差没把Jack马的画像裱起来了，可能这就是命，不为金钱的腐蚀，只为精神的洗礼。

> 2017年10月9号公司成立了。

